@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Anton);
@import url(https://fonts.googleapis.com/css?family=Oswald);
body
{
  //background: rgba(30, 144, 255, 0.7);
  background-color:black;
  font-family:Roboto;
  color:white;
  font-weight:100;
  overflow:hidden;
}
#pagetitle
{
  font-size:40px;
  font-weight:300;
  text-shadow:1px 1px black,2px 5px 3px black;
}

#labelcontainer
{
  background-color:rgba(0,0,255,0.5);
  text-align:center;
  position:relative;
  width:50%;
  top:150px;
  margin:auto;
  //border:1px solid white;
  font-size:50px;
  z-index:4;
}



#entirepage
{
    //background: rgba(30, 144, 255, 0.7);
  opacity:100;
  height:100%;
  width:100%;
  display:block;
  position:absolute;
  z-index:3;
  left:0;
  top:0;
}
@media only screen and (max-width:540px)
{
  #cityinput
  {
    font-size:50px;
  }
}
#cityinput
{
  position:relative;
  top:300px;

  width:100%;
  margin:auto;
  height:400px;
  text-align:center;
  overflow:scroll;
  font-size:200px;
  background-color:rgba(0,0,255,.3);
  color:white;
  border:none;
  outline:none;
  z-index:4;


}
#cityinput:focus
{

}
#cityinput100
{
  position:absolute;
  top:300px;
  width:99.8%;
  height:400px;
  text-align:center;
  font-size:100px;
  background-color:black;
  color:white;
  border:none;
  outline:none;
  z-index:4;
}

::-moz-selection
{
  color: none;
  background: none;
  opacity:0;
}

::selection
{
  color: none;
  background: none;
  opacity:0;
}
#myname
{
  position:absolute;
  left:40px;
  color:white;
  font-weight:100;
  font-size:20px;
  text-shadow:1px 2px 0px black;
}
#inputbox
{
  //background-color:red;
  position:absolute;
  width:100%;
  height:100%;
  top:0;
  left:0;
  z-index:4;
}

body{
  font-family: 'Oswald', sans-serif;
    //background: rgba(30, 144, 255, 0.7);
    background-color:black;
}


#loadingBar
{
  position:relative;
  width:50%;
  background-color:red;
   top:20px;

}

#container
{

  position:relative;
  margin:auto;
  width:40%;

  box-shadow: 0px 1px 10px 2px black;
  text-align:center;
  //background-color:rgba(0,0,50,.3);
  top:100px;
  height:300px;
  min-width:300px;
}
#city
{
  background-color:rgba(0,0,255,.25);
  font-size:50px;
  color:white;
  position:relative;
  margin:auto;
  top:10px;
  width:auto;
  box-shadow:0px 0px 5px black;
  border-radius:5px 5px;
  text-shadow: 1px 2px 1px black, 0px 1px 3px black,0px 2px 1px black;
  text-align:center;
}

#returncontainer
{
  position:relative;
  margin:auto;
  font-family:Oswald;
  top:10%;
  text-align:center;

}
#returnbutton
{
  width:100%;
  background-color:rgba(0,0,255,.25);
  height:50px;
  position:relative;
  font-size:25px;
  margin-top:0px;

  margin-bottom:15px;
  font-weight:500;
  color:white;
  border-radius:5px 5px;
  border-style:none;
}
#country
{
  position:relative;

  width:20px;
  margin:auto;
  font-size:20px;
  top:20px;
}


#weatherblurb
{
  position:relative;
  margin:auto;
  background-color:rgba(255,0,0,.7);
  width:50%;
  font-size:25px;

  box-shadow:0 0 5px .5px red;
  text-align:center;
  text-shadow:0px 0px 1px white;
}
#temperature
{
    margin-top:30px;
    margin-left:auto;
    margin-right:auto;
  background-color:rgba(255, 0, 0, .7);
    //box-shadow:0 0 5px 5px rgba(0, 0, 69, 1);
    font-size:45px;
    text-align:center;
    border-radius:5px 5px;
    position:relative;
    width:75%;
    text-shadow:0px 0px 20px 20px blue;


}
#error
{
  position:relative;
  margin:auto;
  background-color:rgba(0,0,255,0.5);
  text-align:center;
  font-size:40px;
  width:50%;
  top:300px;
}
#returnbuttonerror
{
  width:100%;
  background-color:rgba(255,0,0,.7);
  height:50px;
  position:relative;
  font-size:25px;
  margin-top:0px;

  margin-bottom:15px;
  font-weight:500;
  color:white;
  border-radius:5px 5px;
  border-style:none;
}
#shortForecast
{
  //background-color:red;
  top:20px;
  text-align:center;
  font-size:40px;
  text-shadow:0px 2px 2px black;
  position:relative;
  min-width:250px;
  //background-color:black;
}

